var MyApp = angular.module("MyApp", ["ui.router"]);

MyApp.config(["$stateProvider", "$urlRouterProvider", function($stateProvider, $urlRouterProvider) {
  $urlRouterProvider.otherwise("/intro");

  $stateProvider
    .state("main", {
      url: "/",
      templateUrl: "templates/main/main.html",
      controller: "MainController",
      controllerAs: "main"
    })
    .state("main.intro", {
      url: "intro",
      templateUrl: "templates/intro/intro.html",
      controller: "IntroController",
      controllerAs: "introCtrl"
    })
    .state("main.login", {
      url: "login",
      templateUrl: "templates/login/login.html",
    })
    .state("main.other", {
      url: "other",
      templateUrl: "templates/other/other.html",
    })
    .state("stuff", {
      url: "/stuff",
      templateUrl: "templates/stuff/stuff.html",
      controller: "StuffController",
      controllerAs: "stuffCtrl"
    })
}]);

MyApp.service("ApiService", ["$http", function($http) {

  this.syncMethod = function() {
    return 0;
  }

  this.getThings = function() {
    return $http.get("/thing")
      .then(function (response) {
        return response.data;
      })
      .catch(function (err) {
        console.error("Error ApiService getThings ", err);
        return {};
      })
  }
  
  this.makeMove = function(move) {
    return $http.post("http://localhost:3332/move", move)
      .success(function (data, status, headers, config) {
        console.log("Succesful move");
        console.log(data);
      })
      .error(function (data, status, headers, config) {
        console.error("Error ApiService makeMove ", data);
      })
  }

}])

